<script>
import { Line } from "@antv/g2plot";
import { mapState } from "vuex";
export default {
  data() {
    return {};
  },
  computed: {
    ...mapState({
      dataAll: (state) => state.goods.goods,
    }),
  },

  created() {
    this.$store.dispatch("goods/requestGoodsAll");
  },
  mounted() {
    const unwatch = this.$watch("dataAll", (dataAll) => {
      // console.log(dataAll)
      const line = new Line(this.$refs.container, {
        data: dataAll,
        xField: 'goodsPrice',
        yField:'goodsSales',
        label: {},
        point: {
          size: 5,
          shape: "diamond",
          style: {
            fill: "white",
            stroke: "#2593fc",
            lineWidth: 2,
          },
        },
      });

      line.render();

    });
    
  },
